<!-- components/FilterConditions.vue -->
<template>
  <div class="container">
    <h2>
      Отбор по условиям
      <div
        class="info-icon-wrapper"
        @mouseover="showTooltip"
        @mouseleave="hideTooltip"
      >
        <img
          src="@/assets/img/icon.png"
          alt="Информация"
          class="info-icon"
          style="width: 15px; height: 15px"
        />
        <div v-if="tooltipVisible" class="tooltip">
          <p><strong>Параметры системы:</strong></p>
          <ul>
            <li>μ = 0,01215</li>
            <li>Расстояние между Землей и Луной: 384400 км</li>
          </ul>
        </div>
      </div>
    </h2>
    <div class="params">
      <!-- Ваши существующие поля формы -->
      <div>
        <p>Семейство</p>
        <custom-select
          :options="filteredFamilyLabels"
          :modelValue="selectedFamilyLabel"
          @update:modelValue="onUpdateFamilyLabel"
        />
      </div>
      <div>
        <p>Амплитуда по x (км)</p>
        <from-to filterName="amplitudeX" @update="onUpdateFilter" />
      </div>
      <div>
        <p>Период (дни)</p>
        <from-to filterName="period" @update="onUpdateFilter" />
      </div>
      <div>
        <p>Амплитуда по y (км)</p>
        <from-to filterName="amplitudeY" @update="onUpdateFilter" />
      </div>
      <div>
        <p>Константа Якоби</p>
        <from-to
          filterName="jacobiConstant"
          @update="onUpdateFilter"
        />
      </div>
      <div>
        <p>Амплитуда по z (км)</p>
        <from-to filterName="amplitudeZ" @update="onUpdateFilter" />
      </div>
    </div>
    <button @click="applyFilters">Отобрать</button>
  </div>
</template>
<script>
import CustomSelect from "./customSelect.vue";
import fromTo from "./fromTo.vue";
import { mapState, mapMutations, mapActions } from "vuex";

export default {
  components: { fromTo, CustomSelect },
  props: {
    selectedSystemId: {
      type: Number,
      default: null,
    },
  },
  data() {
    return {
      familyOptions: [
        {
          label: "Горизонтальные орбиты Ляпунова, L1",
          value: "L1.L",
        },
        {
          label: "Горизонтальные орбиты Ляпунова, L2",
          value: "L2.L",
        },
        { label: "Вертикальные орбиты Ляпунова, L1", value: "L1.V" },
        { label: "Вертикальные орбиты Ляпунова, L2", value: "L2.V" },
        { label: "Аксиальные орбиты, L1", value: "L1.A" },
        { label: "Аксиальные орбиты, L2", value: "L2.A" },
        { label: "Гало орбиты, L1", value: "L1.H" },
        { label: "Гало орбиты, L2", value: "L2.H" },
        {
          label: "Двухпериодические квазигоризонтальные орбиты, L1",
          value: "L1.L.2P1",
        },
        {
          label: "Двухпериодические квазигоризонтальные орбиты, L2",
          value: "L2.L.2P1",
        },
        {
          label: "Трехпериодические квазигоризонтальные орбиты, L1",
          value: "L1.L.3P1",
        },
        {
          label: "Трехпериодические квазигоризонтальные орбиты, L2",
          value: "L2.L.3P1",
        },
        {
          label:
            "Четырехпериодические квазигоризонтальные орбиты, L1",
          value: "L1.L.4P1",
        },
        {
          label:
            "Четырехпериодические квазигоризонтальные орбиты, L2",
          value: "L2.L.4P1",
        },
        {
          label:
            "Четырехпериодические квазигоризонтальные орбиты, L1 (2P1.2P1)",
          value: "L1.L.2P1.2P1",
        },
        {
          label:
            "Четырехпериодические квазигоризонтальные орбиты, L2 (2P1.2P1)",
          value: "L2.L.2P1.2P1",
        },
        {
          label:
            "Шестипериодические квазигоризонтальные орбиты, L1 (2P1.3P1)",
          value: "L1.L.2P1.3P1",
        },
        {
          label:
            "Шестипериодические квазигоризонтальные орбиты, L1 (2P1.3P2)",
          value: "L1.L.2P1.3P2",
        },
        {
          label:
            "Шестипериодические квазигоризонтальные орбиты, L2 (2P1.3P1)",
          value: "L2.L.2P1.3P1",
        },
        {
          label:
            "Шестипериодические квазигоризонтальные орбиты, L2 (2P1.3P2)",
          value: "L2.L.2P1.3P2",
        },
        {
          label:
            "Двухпериодические квазигоризонтальные орбиты, L1 (2P1.Tan)",
          value: "L1.L.2P1.Tan",
        },
        {
          label:
            "Двухпериодические квазигоризонтальные орбиты, L2 (2P1.Tan)",
          value: "L2.L.2P1.Tan",
        },
        {
          label: "Двухпериодические квазигало орбиты, L1 (2P1)",
          value: "L1.H.2P1",
        },
        {
          label: "Двухпериодические квазигало орбиты, L1 (2P2)",
          value: "L1.H.2P2",
        },
        {
          label: "Двухпериодические квазигало орбиты, L1 (2P3)",
          value: "L1.H.2P3",
        },
        {
          label: "Двухпериодические квазигало орбиты, L2 (2P1)",
          value: "L2.H.2P1",
        },
        {
          label: "Двухпериодические квазигало орбиты, L2 (2P2)",
          value: "L2.H.2P2",
        },
        {
          label: "Трехпериодические квазигало орбиты, L1 (3P1)",
          value: "L1.H.3P1",
        },
        {
          label: "Трехпериодические квазигало орбиты, L1 (3P2)",
          value: "L1.H.3P2",
        },
        {
          label: "Трехпериодические квазигало орбиты, L1 (3P3)",
          value: "L1.H.3P3",
        },
        {
          label: "Трехпериодические квазигало орбиты, L2 (3P1)",
          value: "L2.H.3P1",
        },
        { label: "Квазипериодические орбиты, L1", value: "L1.Q" },
      ],
      tooltipVisible: false,
    };
  },
  computed: {
    ...mapState(["filters"]),
    filteredFamilyOptions() {
      if (this.selectedSystemId === 1) {
        return this.familyOptions.filter((option) =>
          option.value.includes("L1")
        );
      } else if (this.selectedSystemId === 2) {
        return this.familyOptions.filter((option) =>
          option.value.includes("L2")
        );
      } else {
        return this.familyOptions; // Если systemId не выбран, показываем все
      }
    },
    filteredFamilyLabels() {
      return this.filteredFamilyOptions.map((option) => option.label);
    },
    selectedFamilyLabel: {
      get() {
        const selected = this.filteredFamilyOptions.find(
          (option) => option.value === this.filters.family
        );
        return selected ? selected.label : "";
      },
      set(newLabel) {
        const selected = this.filteredFamilyOptions.find(
          (option) => option.label === newLabel
        );
        if (selected) {
          this.updateFamily(selected.value);
        } else {
          this.updateFamily("");
        }
      },
    },
  },
  watch: {
    selectedSystemId(newVal, oldVal) {
      if (newVal !== oldVal) {
        this.updateFamily(""); // Сбрасываем выбранное семейство
      }
    },
  },
  methods: {
    ...mapMutations(["selectOrbit"]),
    ...mapActions(["updateFilter", "updateFamily"]),
    onUpdateFilter({ filterName, value }) {
      this.updateFilter({ filterName, value });
    },
    onUpdateFamilyLabel(newLabel) {
      const selected = this.filteredFamilyOptions.find(
        (option) => option.label === newLabel
      );
      if (selected) {
        this.updateFamily(selected.value);
      } else {
        this.updateFamily("");
      }
    },
    applyFilters() {
      this.selectOrbit();
      this.$emit("filter-applied");
    },
    showTooltip() {
      this.tooltipVisible = true;
    },
    hideTooltip() {
      this.tooltipVisible = false;
    },
  },
};
</script>
