<template>
  <div class="container-main">
    <displayTypeBlock
      :orbitCoordinates="orbitCoordinates"
      v-model:selectedOption="selectedOption"
      :jacobiConstant="jacobiConstant"
      @update-orbit-coordinates="updateOrbitCoordinates"
      @find-nearest-orbit="findNearestOrbit"
      @update-poincare-plane="updatePoincarePlane"
      @build-poincare-section-with-cj="buildPoincareSectionWithCj"
      @build-parameter-change-graph="buildParameterChangeGraph"
    ></displayTypeBlock>

    <!-- Conditional rendering of components based on selectedOption -->
    <OrbitInformationBuild
      v-if="selectedOption === 'Орбита в 3D'"
      :parentLoading="isLoading"
      @update-orbit-coordinates="updateOrbitCoordinates"
      @update-family="updateFamily"
      @update-jacobi-constant="updateJacobiConstant"
    ></OrbitInformationBuild>

    <OrbitProjections
      v-else-if="selectedOption === 'Проекции орбиты'"
      :orbitId="orbitId"
      @update-orbit-coordinates="updateOrbitCoordinates"
    ></OrbitProjections>

    <NearestOrbitPoincare
      v-else-if="selectedOption === 'Сечение Пуанкаре одной орбиты'"
      :parentLoading="isLoading"
      :poincarePlane="poincarePlane"
      @update-orbit-coordinates="updateOrbitCoordinates"
      @update-family="updateFamily"
      @update-jacobi-constant="updateJacobiConstant"
      :key="orbitId"
    ></NearestOrbitPoincare>

    <!-- New Component for Poincare Section with CJ -->
    <PoincareSectionWithCj
      v-else-if="
        selectedOption === 'Сечение Пуанкаре с константой Якоби'
      "
      :librationPoint="librationPoint"
      :cj="jacobiConstant"
      :d="jacobiError"
      :plane="poincarePlane"
    ></PoincareSectionWithCj>

    <ParameterChangeGraph
      v-if="
        selectedOption === 'График изменения параметра в семействе' &&
        family &&
        paramX &&
        paramY
      "
      :family="family"
      :paramX="paramX"
      :paramY="paramY"
    ></ParameterChangeGraph>
  </div>
</template>

<script>
import OrbitInformationBuild from "@/components/buildPage/OrbitInformationBuild.vue";
import OrbitProjections from "@/components/buildPage/OrbitProjections.vue";
import displayTypeBlock from "@/components/buildPage/displayTypeBlock.vue";
import NearestOrbitPoincare from "@/components/buildPage/NearestOrbitPoincare.vue";
import PoincareSectionWithCj from "@/components/buildPage/PoincareSectionWithCj.vue";
import ParameterChangeGraph from "@/components/buildPage/ParameterChangeGraph.vue";

import { mapState, mapActions } from "vuex";

export default {
  components: {
    OrbitInformationBuild,
    OrbitProjections,
    displayTypeBlock,
    NearestOrbitPoincare,
    PoincareSectionWithCj,
    ParameterChangeGraph,
  },
  data() {
    return {
      orbitCoordinates: {
        x: 0,
        y: 0,
        z: 0,
        vx: 0,
        vy: 0,
        vz: 0,
      },
      selectedOption: "Орбита в 3D",
      family: null,
      paramX: null,
      paramY: null,
      jacobiConstant: null,
      librationPoint: "L1",
      jacobiError: 0.01,
      orbitNotFound: false,
      isLoading: false,
      nearestOrbit: null,
      poincareSections: [],
      poincarePlane: null,
    };
  },
  computed: {
    ...mapState(["orbitId"]),
    orbitData() {
      return this.$store.getters.getOrbitData(this.orbitId);
    },
  },
  methods: {
    ...mapActions(["updateOrbitId", "cacheOrbitData"]),
    updateOrbitCoordinates(newCoordinates) {
      this.orbitCoordinates = { ...newCoordinates };
    },
    updateFamily(newFamily) {
      this.family = newFamily;
    },
    updateJacobiConstant(newCj) {
      this.jacobiConstant = newCj;
    },
    async findNearestOrbit() {
      this.isLoading = true;
      try {
        const { x, y, z, vx, vy, vz } = this.orbitCoordinates;
        const response = await fetch(
          `https://orbital-catalog.auditory.ru/api/v1/trajectories/nearest?x=${
            x || 0
          }&y=${y || 0}&z=${z || 0}&vx=${vx || 0}&vy=${vy || 0}&vz=${
            vz || 0
          }`
        );

        const data = await response.json();

        if (response.ok) {
          if (data.orbit_id !== undefined) {
            this.updateOrbitId(data.orbit_id);
            this.orbitNotFound = false;
            this.cacheOrbitData({
              id: data.orbit_id,
              orbitInfo: null,
              trajectory: data.v,
            });
          } else {
            throw new Error("Invalid response format from server");
          }
        } else if (
          response.status === 404 &&
          data.detail === "not found"
        ) {
          this.updateOrbitId(null);
          this.orbitNotFound = true;
        } else {
          throw new Error("Error finding nearest orbit");
        }
      } catch (error) {
        console.error("Error finding nearest orbit:", error);
        this.updateOrbitId(null);
        this.orbitNotFound = true;
      } finally {
        this.isLoading = false;
      }
    },
    updateNearestOrbit(newOrbit) {
      this.nearestOrbit = newOrbit;
    },
    updatePoincareSections(newSections) {
      this.poincareSections = newSections;
    },
    updatePoincarePlane(newPlane) {
      this.poincarePlane = newPlane;
    },
    buildPoincareSectionWithCj(params) {
      this.librationPoint = params.librationPoint;
      this.jacobiConstant = params.cj;
      this.jacobiError = params.d;
      this.poincarePlane = params.plane;
      this.selectedOption = "Сечение Пуанкаре с константой Якоби";
    },
    buildParameterChangeGraph(params) {
      this.family = params.family;
      this.paramX = params.paramX;
      this.paramY = params.paramY;
      this.selectedOption = "График изменения параметра в семействе";
    },
  },
  watch: {
    orbitId(newVal) {
      if (newVal && this.selectedOption === "Орбита в 3D") {
        // The child component will fetch data from Vuex store
      }
      if (newVal && this.selectedOption === "Проекции орбиты") {
        // The OrbitProjections component will update automatically via props
      }
    },
  },
  mounted() {
    if (this.orbitId && this.selectedOption === "Орбита в 3D") {
      // The child component will fetch data from Vuex store
    }
    if (this.orbitId && this.selectedOption === "Проекции орбиты") {
      // The OrbitProjections component will fetch data from Vuex store
    }
  },
};
</script>

<style scoped>
.container-main {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.no-orbit-found,
.loading-spinner {
  text-align: center;
  font-size: 18px;
  color: red;
}
</style>
