<template>
  <div class="container-main">
    <system-selection
      @update:selected-system="updateSelectedSystemId"
    ></system-selection>
    <selection-by-conditions
      :selected-system-id="selectedSystemId"
      @filter-applied="updateMapData"
    ></selection-by-conditions>
    <MapImg
      v-if="selectedOrbit == 1"
      ref="MapImg"
      @orbit-applied="updateOrbitInfo"
    ></MapImg>
    <OrbitInformation
      v-if="informationOrbit"
      ref="OrbitInformation"
    ></OrbitInformation>
  </div>
</template>

<script>
import MapImg from "@/components/mainPage/MapImg";
import OrbitInformation from "@/components/mainPage/OrbitInformation";
import systemSelection from "@/components/mainPage/systemSelection.vue";
import SelectionByConditions from "@/components/mainPage/selectionByConditions.vue";
import { mapState } from "vuex";

export default {
  components: {
    MapImg,
    OrbitInformation,
    systemSelection,
    SelectionByConditions,
  },
  data() {
    return {
      selectedSystemId: null,
    };
  },
  methods: {
    updateSelectedSystemId(id) {
      this.selectedSystemId = id;
      this.updateMapData(); // Обновляем карту при изменении системы
    },
    updateMapData() {
      if (this.$refs.MapImg) {
        this.$refs.MapImg.fetchOrbits();
      }
    },
    updateOrbitInfo() {
      if (this.$refs.OrbitInformation) {
        this.$refs.OrbitInformation.submit();
      }
    },
  },
  computed: {
    ...mapState(["selectedOrbit", "informationOrbit"]),
  },
  watch: {
    selectedOrbit(newValue) {
      if (newValue === 1) {
        this.$nextTick(() => {
          this.updateMapData();
        });
      }
    },
  },
};
</script>

<style scoped>
.container-main {
  display: flex;
  flex-direction: column;
  gap: 48px;
}
</style>
