<template>
  <div class="container">
    <p
      v-for="system in systems"
      :key="system.id"
      @click="selectSystem(system)"
      :class="{
        selected: selectedSystem && selectedSystem.id === system.id,
      }"
      v-html="`${system.name}<br>${system.detail}`"
    ></p>
  </div>
</template>
<script>
export default {
  data() {
    return {
      selectedSystem: null,
      systems: [
        { id: 1, name: "Земля-Луна", detail: "точка L1" },
        { id: 2, name: "Земля-Луна", detail: "точка L2" },
      ],
    };
  },
  methods: {
    selectSystem(system) {
      this.selectedSystem = system;
      this.$emit("update:selected-system", system.id);
    },
  },
};
</script>

