import { createStore } from "vuex";

export default createStore({
  state: {
    selectedOrbit: 0,
    informationOrbit: 0,
    orbitId: null,
    filters: {
      amplitudeX: { from: null, to: null },
      period: { from: null, to: null },
      amplitudeY: { from: null, to: null },
      jacobiConstant: { from: null, to: null },
      amplitudeZ: { from: null, to: null },
      family: null,
    },
    orbitCache: {},
    poincarePlane: "x = 0",
  },
  mutations: {
    selectOrbit(state) {
      state.selectedOrbit = 1;
    },
    infoOrbit(state) {
      state.informationOrbit = 1;
    },
    resetInformationOrbit(state) {
      state.informationOrbit = 0;
    },
    setOrbitId(state, id) {
      state.orbitId = id;
    },
    setPoincarePlane(state, plane) {
      state.poincarePlane = plane;
    },
    addOrbitToCache(state, { id, orbitInfo, trajectory }) {
      state.orbitCache[id] = { orbitInfo, trajectory };
    },
    addTrajectoryToCache(state, { id, trajectory }) {
      if (!state.orbitCache[id]) {
        state.orbitCache[id] = {};
      }
      state.orbitCache[id].trajectory = trajectory;
    },
    addOrbitInfoToCache(state, { id, orbitInfo }) {
      if (!state.orbitCache[id]) {
        state.orbitCache[id] = {};
      }
      state.orbitCache[id].orbitInfo = orbitInfo;
    },
    setFilter(state, { filterName, value }) {
      state.filters[filterName] = value;
    },
    setFamily(state, value) {
      state.filters.family = value;
    },
    setAllOrbitsData(state, data) {
      state.allOrbitsData = data;
    },
    setSelectedOrbitsData(state, data) {
      state.selectedOrbitsData = data;
    },
    setSelectedOrbitId(state, orbitId) {
      state.selectedOrbitId = orbitId;
    },
  },
  actions: {
    updateOrbitId({ commit }, id) {
      commit("setOrbitId", id);
    },
    updateFilter({ commit }, { filterName, value }) {
      commit("setFilter", { filterName, value });
    },
    updateFamily({ commit }, value) {
      commit("setFamily", value);
    },
    cacheOrbitData({ commit }, payload) {
      commit("addOrbitToCache", payload);
    },
    cacheTrajectoryData({ commit }, payload) {
      commit("addTrajectoryToCache", payload);
    },
    cacheOrbitInfo({ commit }, payload) {
      commit("addOrbitInfoToCache", payload);
    },
  },
  getters: {
    orbitId: (state) => state.orbitId,
    filters: (state) => state.filters,
    getOrbitData: (state) => (id) => {
      return state.orbitCache ? state.orbitCache[id] : undefined;
    },
    getOrbitInfo: (state) => (id) => {
      return state.orbitCache[id]?.orbitInfo || null;
    },
    getTrajectory: (state) => (id) => {
      return state.orbitCache[id]?.trajectory || null;
    },
  },
});
